.SideBar {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 0px;
  display: none;
  height: 100%;
  background-color: gray;
  margin-top: 30px;
  /* adds animation for all transitions */
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out; }
